/**
 * 
 *  
 * @desc: 
 * @return: eg: '2018/04/09 21:31:00'
 * @param {Date} date 
 */
export function formatTimeNow() {
	let date = new Date();
	const year = date.getFullYear();
	const month = date.getMonth() + 1;
	const day = date.getDate();
	const hour = date.getHours();
	const minute = date.getMinutes();
	const second = date.getSeconds();
	return [year, month, day].map(formatNumber).join('/') + ' ' + [hour, minute, second].map(formatNumber).join(':');
}

/**
 * 
 *  
 * @desc: 
 * @return: eg: '2018/04/09 21:31:00'
 * @param {Date} date 
 */
export function formatTime(date) {
	const year = date.getFullYear();
	const month = date.getMonth() + 1;
	const day = date.getDate();
	const hour = date.getHours();
	const minute = date.getMinutes();
	const second = date.getSeconds();
	return [year, month, day].map(formatNumber).join('/') + ' ' + [hour, minute, second].map(formatNumber).join(':');
}
/**
 *  
 * @desc: 
 * @return: eg: '2018/04/09 21:31:00'
 * @param {Date} date 
 */
export function formatDate(date) {
	const year = date.getFullYear();
	const month = date.getMonth() + 1;
	const day = date.getDate();
	return [year, month, day].map(formatNumber).join('/');
}; //strDate 


export function getDate(strDate) {
	var st = strDate;
	var a = st.split(" "); //TT
	var b = a[0].split("-");
	var c = a[1].split(":");
	var date = new Date(b[0], b[1], b[2], c[0], c[1], c[2]);
	return date;
};

/**
 * @description   
 * @param {String} _dateTimeString  YYYY-MM-DD hh:mi:ss
 * @return {String} _value  YYYY/MM/DD
 */
export function dateTimeStringToDateString(_dateTimeString){
	let _value = _dateTimeString.replace(/\-/g, "/");
	let _tmpValue = new Date(_value);
	return formatDate(_tmpValue);
}

/**
 * @desc: 
 * @return: n > 10 [eg: 12] => 12 | n < 10 [eg: 3] => '03'
 * @param {*} n 
 */
export function formatNumber(n){
	n = n.toString();
	return n[1] ? n : '0' + n;
};

/**
 * @description  YYYYMMDDHHMISS
 * @return {YYYYMMDDHHMISS} YYYYMMDDHHMISS
 * @param {}   
 */
export function getDateYYYYMMDDHHMISS() {
	let date = new Date();
	let year = date.getFullYear();
	let month = date.getMonth() + 1;
	let day = date.getDate();
	let hour = date.getHours();
	let minute = date.getMinutes();
	let second = date.getSeconds();
	if (month < 10) {
		month = '0' + month;
	}
	if (day < 10) {
		day = '0' + day;
	}
	if (hour < 10) {
		hour = '0' + hour;
	}
	if (minute < 10) {
		minute = '0' + minute;
	}
	if (second < 10) {
		second = '0' + second;
	}
	return year + "" + month + "" + day + "" + hour + "" + minute + "" + second;
};

/**
 * @description  
 * @param {Object} _date 
 * @param {Object} _year 
 * @return {Date} 
 */
export function addYear(_date, _year) {
	let year = _date.getFullYear() + _year; //
	let mon = _date.getMonth(); //
	let da = _date.getDate(); //
	let h = _date.getHours(); //
	let m = _date.getMinutes(); //
	let s = _date.getSeconds(); //
	let newDate = new Date(year, mon, da, h, m, s);
	return newDate;
};

/**
 * @description 
 * @param {Object} _date 
 * @param {Object} _month 
 * @return {Date} 
 */
export function addMonth(_date, _month) {
	let year = _date.getFullYear(); //
	let mon = _date.getMonth() + _month; //
	let da = _date.getDate(); //
	let h = _date.getHours(); //
	let m = _date.getMinutes(); //
	let s = _date.getSeconds(); //
	let newDate = new Date(year, mon, da, h, m, s);
	return newDate;
};
/**
 * @description 
 * @param {Object} _date 
 * @param {Object} _day 
 * @return {Date} 
 */
export function addDay(_date, _day) {
	let year = _date.getFullYear(); //
	let mon = _date.getMonth(); //
	let da = _date.getDate() + _day; //
	let h = _date.getHours(); //
	let m = _date.getMinutes(); //
	let s = _date.getSeconds(); //
	let newDate = new Date(year, mon, da, h, m, s);
	return newDate;
};

/**
 * @description 
 * @param {Object} _date 
 * @param {Object} _hour 
 * @return {Date} 
 */
export function addHour(_date, _hour) {
	let year = _date.getFullYear(); //
	let mon = _date.getMonth(); //
	let da = _date.getDate(); //
	let h = _date.getHours() + _hour; //
	let m = _date.getMinutes(); //
	let s = _date.getSeconds(); //
	let newDate = new Date(year, mon, da, h, m, s);
	return newDate;
};
/**
 * @description 
 * @param {Object} _date 
 * @param {Object} _minute 
 * @return {Date} 
 */
export function addMinutes(_date, _minute) {
	let year = _date.getFullYear(); //
	let mon = _date.getMonth(); //
	let da = _date.getDate(); //
	let h = _date.getHours(); //
	let m = _date.getMinutes() + _minute; //
	let s = _date.getSeconds(); //
	let newDate = new Date(year, mon, da, h, m, s);
	return newDate;
};
/**
 * @description 
 * @param {Object} _date 
 * @param {Object} _second 
 * @return {Date} 
 */
export function addSeconds(_date, _second) {
	let year = _date.getFullYear(); //
	let mon = _date.getMonth(); //
	let da = _date.getDate(); //
	let h = _date.getHours(); //
	let m = _date.getMinutes(); //
	let s = _date.getSeconds() + _second; //
	let newDate = new Date(year, mon, da, h, m, s);
	return newDate;
}