import {
	requsetHcServerToLogin
} from './LoginHcServer.js'


export function doLogin(callback = () => {}) {
	let that = this;
	wx.login({
		success: function(loginRes) {
			if (loginRes.code) {
				// TODO
				//
				requsetHcServerToLogin(loginRes, callback);
			} else {
				//  code 
				//that.coreUtil.showInfo('');
				console.log('wx.logincode');
			}
		},
		fail: function(error) {
			//  wx.login 
			//that.coreUtil.showInfo('' + error);
			console.log(error);
		}
	});
}
