import {
	requestNoAuth
} from '../api/java110Request.js'
import url from '../constant/url.js'
import mapping from '../constant/MappingConstant.js'
/**
 *  HC 
 */
export function requsetHcServerToLogin(loginRes, callback = () => {}) {
	let defaultRawData = '{"nickName":"","gender":1,"language":"","city":"","province":"","country":"","avatarUrl":""}'; // 
	console.log('', loginRes);
	let _appId = '';

	let accountInfo = uni.getAccountInfoSync();
	_appId = accountInfo.miniProgram.appId;





	requestNoAuth({
		url: url.loginUrl,
		method: 'post',
		data: {
			code: loginRes.code,
			// 
			userInfo: JSON.parse(defaultRawData),
			// 
			signature: '',
			// 
			encryptedData: '',
			// 
			iv: '', // 
			appId: _appId

		},
		success: function(res) {
			if (res.statusCode == '401') {
				let data = res.data;
				uni.setStorageSync(mapping.CURRENT_OPEN_ID, data.openId);
				wx.reLaunch({
					url: '/pages/showlogin/showlogin'
				});
				return;
			}

			res = res.data;

			if (res.result == 0) {
				//that.globalData.userInfo = res.userInfo;
				console.log(res.userInfo);
				wx.setStorageSync(mapping.USER_INFO, JSON.stringify(res.userInfo));
				let date = new Date();
				let year = date.getFullYear(); //

				let mon = date.getMonth(); //

				let da = date.getDate(); //

				let h = date.getHours() + 1; //

				let m = date.getMinutes(); //

				let s = date.getSeconds(); //

				console.log("", year, mon, da, h, m, s); //

				let afterOneHourDate = new Date(year, mon, da, h, m, s); //30s

				console.log("afterOneHourDate", afterOneHourDate);
				wx.setStorageSync(mapping.LOGIN_FLAG, {
					sessionKey: res.sessionKey,
					expireTime: afterOneHourDate.getTime()
				});
				wx.setStorageSync(mapping.TOKEN, res.token);
				callback();
			} else {
				
			}
		},
		fail: function(error) {
			// 
			if (error.statusCode == 401) {
				uni.reLaunch({
					url: '/pages/login/login'
				});
				return;
			}
			console.log(error);
		}
	});
} //  
